@import "../../style/sass/mixins";
@import "../../style/sass/veriables";

.title {
  margin: 0 0 30px;
  @include text($fontFamilyTitle, 34px, 1.2, $textColor, 700);
  text-transform: capitalize;
  letter-spacing: 5px;
  position: relative;
  text-align: center;
   .h2 {
    margin: 0;
  }
  &::before {
    content: "";
    position: absolute;
    background: $primaryColor;
    width: 55px;
    height: 1px;
    bottom: -30px;
    left: 50%;
    transform: translate(-50%,50%);
  }
}